@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500&display=swap);
.containerLogin{
  background-color: #1b1f27;
  font-family: 'Poppins', sans-serif;
  min-width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  color: #cbd0f7
}

.card{
  background-color: none;
}

.area-logo{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.login{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #181920;
  width: 450px;
  max-height: 620px;
  border-radius: 10px;
}

.login .form{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 5px 30px;
}
.login .input{
  background-color: #252a34;
  border: none;
  outline: none;
  border-radius: 8px;
  color: #cbd0f7;

}

.login img{
  width: 85px;
  height: auto;
  margin-bottom: 10px;
}

.input::-webkit-input-placeholder{
  font-size: 14px;
}

.input:-ms-input-placeholder{
  font-size: 14px;
}

.input::-ms-input-placeholder{
  font-size: 14px;
}

.input::placeholder{
  font-size: 14px;
}

.form .bt{
  display: block;
  background-color: #5568fe;
  font-size: 15px;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}

a{
  color: #5568fe;
  text-decoration: none;
  margin-left: 5px;
  cursor: pointer;
}





