.navbar-logo{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}


.navbar-logo img{
  width: 25px;
  margin-right: 5px;
}

.MainContainer::after {
  content: "";
  background: url("/Logo.png");
  opacity: 0.08;
  top: 20%;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;   
  background-repeat: no-repeat;
  background-position: center;
  background-size: 35%;
}

.photo .delete-top-button{
  visibility: hidden;

}

.photo:hover .delete-top-button{
  visibility: visible;
}

.TableDetails{
	text-shadow: 0.5px 0.5px 0px #fff;
	border-radius:3px;
	width: 100%;

}

.delete-top-button {
  position: absolute;
  top: 10px;
  right: 10px;
}

@media (max-width: 425px) {
	.TableDetailsMaster{
		display: d-flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	#photo{
		width: 300px;
		justify-content: center;
	}
}
@media (max-width: 767px) {
	.TableDetailsMaster{
		display: d-flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	#photo{
		max-width: 350px;
		justify-content: center;
	}
	.TableDetails{
		width: auto;
	}
}
@media (max-width: 1023px) {
	.TableDetailsMaster{
		display: flex;
		flex-wrap: wrap;	
		justify-content: center;
	}
	#photo{
		max-width: 550px;
	}
	.TableDetails{
		width: auto;
	}
}
@media (min-width: 1024px) {
	#app{
		display: flex;
		flex-wrap: wrap;
		width: auto;
	}
	#photo{
		max-width: 350px;
	}
	.TableDetails{
		margin-right: 2rem;
		width: auto;
	}
}
@media (min-width: 1200px) {
	.TableDetails{
		margin-right: 2rem;
		min-width: 450px;
	}
	#app{
		width: auto;
		display: flex;
		flex-wrap: wrap;
	}
	#photo{
		width: 350px;
	}
}




